.title {
  width: 39.1vw; }

.navigation a {
  padding: 0px 20px; }

.signature {
  padding: 20px;
  flex-direction: column !important; }

.social {
  padding: 20px;
  flex-direction: column !important; }

.social div {
  flex-direction: row; }

.social a img {
  padding: 0 10px;
  height: 40px;
  width: auto; }

.social a img:hover {
